<template>
  <div>
    <ul class="items">
      <li class="item">当前筛选条件</li>
      <li class="item">年份：
        <span :class="needYear?'need-year':'buxian'" @click="toggleYear">不限</span>
        <el-date-picker v-model="year" type="year" placeholder="选择年" v-show="needYear"></el-date-picker>
      </li>
      <li class="item">月份：
        <span :class="needMonth?'need-year':'buxian'" @click="toggleMonth">不限</span>
        <el-date-picker v-model="month" type="month" placeholder="选择月" v-show="needMonth"></el-date-picker>
      </li>
      <li class="item">项目名称：
        <span :class="needName?'need-year':'buxian'" @click="toggleName">不限</span>
        <el-input v-model="name" placeholder="请输入项目名称" style="width: 200px;" v-show="needName"></el-input>
      </li>
      <li class="item">项目类型：
        <span :class="needType?'need-year':'buxian'" @click="toggleType">不限</span>
        <el-input v-model="type" placeholder="请输入项目类型" style="width: 200px;" v-show="needType"></el-input>
      </li>
    </ul>
    <el-button type="primary">按条件筛选</el-button>

    <p class="text-center caption-color">研发项目情况表</p>
    <el-table :data="tableData4" border style="width: 100%"
      :header-cell-style="hdStyle" :cell-style="cellStyle">
      <el-table-column v-for="(item ,index) in tableCloumn4" :key="index" :prop="item.prop" :label="item.label"/>
      <el-table-column
        fixed="right"
        label="操作"
        width="80">
        <template slot-scope="scope">
          <el-button @click="getResDetail(scope.row)" type="text" size="small">查看详情</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog
      title="项目详情"
      :visible.sync="dialogVisible"
      width="60%"
      :before-close="handleClose">
      <el-table :data="tableData1" border style="width: 100%"
        :header-cell-style="hdStyle" :cell-style="cellStyle">
        <el-table-column v-for="(item ,index) in tableCloumn1" :key="index" :prop="item.prop" :label="item.label"/>
      </el-table>

      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">关 闭</el-button>
        <!-- <el-button type="primary" @click="dialogVisible = false">确 定</el-button> -->
      </span>
    </el-dialog>
  </div>
</template>

<script>
import cellStyle from '../mixin/cellStyle'
export default {
  mixins: [cellStyle],
  data() {
    return {
      month: new Date(),
      tableCloumn4: [
        { prop: 'col1', label: '研发项目' },
        { prop: 'col2', label: '研发项目类型' },
        { prop: 'col3', label: '当前阶段' },
        { prop: 'col4', label: '进度' },
        { prop: 'col5', label: '研发费用预算' },
        { prop: 'col6', label: '实际投入' },
        { prop: 'col7', label: '预算使用进度' },
        { prop: 'col8', label: '累计延期时间' },
      ],
      tableData4: [
        { col1: 'P20190103', col2: '', col3: '', col4: '', col5: '', col6: '', col7: '', col8: ''},
        { col1: 'P20190104', col2: '平台技术-硬件', col3: 'PAC评审', col4: '5%', col5: '18000', col6: '1000', col7: '5.56%', col8: '0天'},
      ],
      tableCloumn1: [
        { prop: 'col1', label: '研发阶段' },
        { prop: 'col2', label: '状态' },
        { prop: 'col3', label: '预计完成时间' },
        { prop: 'col4', label: '累计延期时间' },
      ],
      tableData1: [
        { col1: '立项', col2: '', col3: '', col4: ''},
        { col1: 'PAC评审', col2: '', col3: '', col4: ''},
        { col1: '原理设计', col2: '', col3: '', col4: ''},
        { col1: '元器件采购', col2: '', col3: '', col4: ''},
        { col1: '样机组装', col2: '', col3: '', col4: ''},
        { col1: '调试', col2: '', col3: '', col4: ''},
        { col1: '内部测试', col2: '', col3: '', col4: ''},
        { col1: '公司验收', col2: '', col3: '', col4: ''},
      ],
      year: new Date(),
      month: new Date(),
      name: '',
      type: '',
      needYear: true,
      needMonth: true,
      needName: false,
      needType: false,
      dialogVisible: false
    }
  },
  methods: {
    toggleYear() {
      this.needYear = !this.needYear
    },
    toggleMonth() {
      this.needMonth = !this.needMonth
    },
    toggleName() {
      this.needName = !this.needName
    },
    toggleType() {
      this.needType = !this.needType
    },
    getResDetail(i) {
      console.log(i)
      this.dialogVisible = true
    },
    handleClose(done) {
      done()
      // this.$confirm('确认关闭？')
      //   .then(_ => {
      //     done();
      //   })
      //   .catch(_ => {});
    }
  }
}
</script>

<style lang="scss" scoped>
.items {
  border: 1px solid #eee;
  .item {
    &:first-child {
      background-color: #eee;
    }
    &:not(:first-child) {
      font-weight: 600;
    }
    height: 50px;
    padding: 0 20px;
    line-height: 50px;
    font-size: 15px;
    .buxian {
      padding: 0 20px;
      color: #a44;
      font-weight: normal;
      cursor: pointer;
    }
    .need-year {
      padding: 0 20px;
      color: #777;
      font-weight: normal;
      cursor: pointer;
    }
  }
}

</style>



